import React, { useEffect, useState } from 'react';
import * as d3 from 'd3';
import cloud from 'd3-cloud';
import Spinner from 'react-bootstrap/Spinner';
import Table from 'react-bootstrap/Table';
import Button from 'react-bootstrap/Button';
import PlaylistTableModal from './PlaylistTableModal';


const PlaylistsTable = () => {
  const [data, setData] = useState([]);
  const [loading, setLoading] = useState(true);

  useEffect(() => {
    const fetchData = async () => {
      try {
        const response = await fetch('/playlistsx');
        if (response.status === 401) {
          // Redirect the user to the login page or display an error message
          window.location.href = '/';
          return;
        }
        const jsonData = await response.json();
        setData(jsonData);
        setLoading(false);
      } catch (error) {
        console.error(error);
        // Handle other errors here
      }
    };
  
    fetchData();
  }, []);
  
 
  return (
    <div className="container">
      <h1 className="cover-heading">Pick a playlist</h1>
      <h2>.. and add all (or a subset) of its song to the new playlist for running </h2>
      {loading ? (
        <div className="d-flex justify-content-center align-items-center" style={{ height: '400px' }}>
          <Spinner animation="border" variant="primary" />
        </div>
      ) : (
        <Table responsive bordered variant="dark">
        <thead>
          <tr>
            <th style={{ color: 'steelblue' }}>Name</th>

            <th style={{ color: 'steelblue' }}>P</th>

          </tr>
        </thead>
        <tbody>
          {data.map((d, i) => (
            <tr key={i}>
              <td>
              <PlaylistTableModal linkText={d.name} list_id={d.id} list_name={d.name} modalTitle={"Playlist:"+d.name} width="90%" height="90%" />
              </td>
              <td>
              {d.numberOfTracks}
              </td>

            </tr>
          ))}
        </tbody>
      </Table>

      )}
    </div>
  );
};

export default PlaylistsTable;
