import React, { useState, useEffect, useRef } from 'react';
import * as d3 from 'd3';
import Spinner from 'react-bootstrap/Spinner';
import Table from 'react-bootstrap/Table';
import Button from 'react-bootstrap/Button';
import Toast from 'react-bootstrap/Toast';

import RangeSlider from 'react-bootstrap-range-slider';
import LinkModal from './LinkModal';

import { useContext } from 'react';
import { GlobalStateContext } from './GlobalState';
import PlaylistSaveModal from './PlaylistSaveModal';

const SqueezeMain = () => {
  const [data, setData] = useState([]);
  const [dataBeforeFilter, setDataBeforeFilter] = useState([]);
  const [isLoading, setIsLoading] = useState(true);
  const svgRef = useRef(null);
  const [sortOrder, setSortOrder] = useState('asc');
  const [minTempo, setMinTempo] = useState(0); // Add state for minimum tempo
  const [maxTempo, setMaxTempo] = useState(200); // Add state for maximum tempo

  const { addListToGlobalList } = useContext(GlobalStateContext);
  const [showToast, setShowToast] = useState(false);
  const { globalList } = useContext(GlobalStateContext);
  const { clearGlobalList } = useContext(GlobalStateContext);
  // Add a function to filter data based on the tempo range
  const filterDataByTempo = (min, max) => {
    return dataBeforeFilter.filter(d => d.tempo >= min && d.tempo <= max);
  };
  const handleDelete = (index) => {
    const newSongs = [...data];
    newSongs.splice(index, 1);
    setData(newSongs);
  };

  // Add functions to handle tempo range changes
  const handleMinTempoChange = (e) => {
    setMinTempo(e.target.value);
    const filteredData = filterDataByTempo(minTempo, maxTempo);
    setData(filteredData)
    renderChart(filteredData)
  };

  const handleMaxTempoChange = (e) => {
    setMaxTempo(e.target.value);
    const filteredData = filterDataByTempo(minTempo, maxTempo);
    setData(filteredData)
    renderChart(filteredData)
  };

  // ... (rest of the code remains unchanged)
 const handleSortAsc = () => {
    setSortOrder('asc');
    const sortedData = [...data].sort((a, b) => {
      return a.tempo - b.tempo;
    });
    setData(sortedData);
    renderChart(sortedData); // Call the renderChart function with the sorted data
   
  };

  const handleSortDesc = () => {
    setSortOrder('desc');
    const sortedData = [...data].sort((a, b) => {
      return b.tempo - a.tempo;
    });
    setData(sortedData);
    renderChart(sortedData); // Call the renderChart function with the sorted data
  };

  const handleSortHalfAscAndHalfDesc = () => {
    const sortedData = [...data].sort((a, b) => a.tempo - b.tempo);
    const totalDuration = sortedData.reduce((sum, song) => sum + song.duration_s, 0);
    const halfDuration = Math.floor(totalDuration / 2);
  
    let currentDuration = 0;
    let halfIndex = -1;
    for (let i = 0; i < sortedData.length; i++) {
      currentDuration += sortedData[i].duration_s;
      if (currentDuration >= halfDuration) {
        halfIndex = i;
        break;
      }
    }
  
    const firstHalf = sortedData.slice(0, halfIndex + 1);
    const secondHalf = sortedData.slice(halfIndex + 1).reverse();
    const sortedHalfData = [...firstHalf, ...secondHalf];
  
    setData(sortedHalfData);
    renderChart(sortedData); // Call the renderChart function with the sorted data
  };
  const handleSortFartlek1 = () => {
    const sortedData = [...data].sort((a, b) => a.tempo - b.tempo);
  
    
    let mixed=[];
    let i2 = (sortedData.length-1);
    for (let i = 0; i < (sortedData.length/2); i++) {

      if(i<((sortedData.length/2)<<0)){
        mixed.push(sortedData[i]);
        mixed.push(sortedData[i2]);
        i2=i2-1
      }
        else{
          i=i+1;
          mixed.push(sortedData[i]);
        }
    }

    setData(mixed);
    renderChart(mixed); // Call the renderChart function with the sorted data
  };





  const renderChart = (data) => {
    const svg = d3.select(svgRef.current);
    svg.selectAll('*').remove(); // Clear previous chart elements
    const margin = { top: 20, right: 20, bottom: 30, left: 50 };
    const width = svg.attr('width') - margin.left - margin.right;
    const height = svg.attr('height') - margin.top - margin.bottom;
    const x = d3.scaleBand()
      .range([0, width])
      .domain(data.map(d => d.name))
      .padding(0.1);
    const y = d3.scaleLinear()
      .range([height, 0])
      .domain([0, d3.max(data, d => d.tempo)]);
    svg.append('g')
      .attr('transform', `translate(${margin.left}, ${margin.top})`)
      .call(d3.axisLeft(y));
    svg.selectAll('.lollipop')
      .data(data)
      .enter()
      .append('circle')
      .attr('class', 'lollipop')
      .attr('cx', d => x(d.name) + x.bandwidth() / 2 + margin.left)
      .attr('cy', d => y(d.tempo) + margin.top)
      .attr('r', 5)
      .attr('fill', 'steelblue');
  };

  useEffect(() => {
    setIsLoading(true);
    setData(globalList);
    setDataBeforeFilter(globalList);
    setIsLoading(false);
  }, []);

  useEffect(() => {
    // Do something when globalStateValue changes
    console.log('Squeze-main- Global state value changed:', globalList);
    setData(globalList);
  }, [globalList]);


  useEffect(() => {
    if (!isLoading) {
      renderChart(data);
    }
  }, [data, isLoading]);

  const totalDurationInSeconds = data.reduce((acc, song) => acc + song.duration_s, 0);
  const totalDurationInMinutes = Math.round(totalDurationInSeconds / 60);
  
  const handleClearPlaylist = () => {
    clearGlobalList();
  };

 return (
    <div className="container" style={{ backgroundColor: 'black', color: 'white' }}>
      
      {isLoading ? (
        <Spinner animation="border" role="status">
          <span className="visually-hidden">Loading...</span>
        </Spinner>
      ) : (
        <>
          {/* Add button for sorting */}
          <p>Total Duration: 
          {totalDurationInMinutes}
          {" minutes"}
          </p>
          <svg ref={svgRef} width={250} height={200} />

        <div style={{ maxWidth: '180px' }}>
          <div className="row" >
            <div className="col-3">
            <label>Run Boost </label>
            </div>
            <div className="col-9">
              <div className="row">

                    <div className="col-5">
                    <label>Min: {minTempo}</label>
                    </div>
                    <div className="col-7">
                              <RangeSlider
                            value={minTempo}
                            min={0}
                            max={200}
                            onChange={handleMinTempoChange}
                            tooltip={'off'}
                            variant={'primary'}
                          />
                    </div>

            
              </div>
              <div className="row">
                      <div className="col-5">
                    <label>Max : {maxTempo}</label>
                    </div>
                    <div className="col-7">
                            <RangeSlider
                          value={maxTempo}
                          min={0}
                          max={200}
                          onChange={handleMaxTempoChange}
                          tooltip={'off'}
                          variant={'primary'}
                        />
                    </div>

              </div>
            </div>
          
            <br/>
        </div>

        <div className="row">
            <div className="col-3">
            <label>Run Shape: </label>
            </div>
            <div className="col-2">
                <Button variant="primary" onClick={handleSortAsc}>
                Up
              </Button>
              </div>
              <div className="col-3">
                    <Button variant="primary" onClick={handleSortDesc}>
                  Down
                </Button>
              </div>
              <div className="col-2">
                    <Button variant="primary" onClick={handleSortHalfAscAndHalfDesc}>
                  Arc
                </Button>
              </div>
              <div className="col-2">
              <Button variant="primary" onClick={handleSortFartlek1}>
            FTK
          </Button>
              </div>

        </div>
        </div>
<br/>
<br/>

<br/>

          <Table responsive bordered variant="dark">
            <thead>
              <tr>
                <th style={{ color: 'steelblue' }}>Name</th>
                <th style={{ color: 'steelblue' }}></th>
                <th style={{ color: 'steelblue' }}>P</th>
                <th style={{ color: 'steelblue' }}>BPM</th>
              </tr>
            </thead>
            <tbody>
              {data.map((d, i) => (
                <tr key={i}>
                  <td>{d.name}</td>
                  <td>
                <Button  onClick={() => handleDelete(i)}>
                  x
                </Button>
                </td>
                  <td>
                      <LinkModal linkText="P" url={d.preview_url} modalTitle={d.artist+"-"+d.name} width="250" height="100" />
                  </td>
                  <td>{Math.round(d.tempo)}</td>
                </tr>
              ))}
            </tbody>
          </Table>

         
  <br/>
  <PlaylistSaveModal list={data} linkText="Save your playlist" modalTitle="Saving playlist" />
  <Button variant="secondary" onClick={handleClearPlaylist}>
        Clear Playlist
        </Button>

        </>
      )}
    </div>
  );
};




export default SqueezeMain;
